.clip01 {
  position: absolute;
  height: 100%;
  width: 100%;
  background: #07767c;
  -webkit-clip-path: polygon(0 0, 15% 0, 80% 100%, 0 100%);
          clip-path: polygon(0 0, 15% 0, 80% 100%, 0 100%);
  z-index: 10;
}

.clip02 {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-clip-path: polygon(14.8% 0%, 60% 0, 40% 50%);
          clip-path: polygon(14.8% 0%, 60% 0, 40% 50%);
  background: #3f769b;
  background-image: linear-gradient(to top right, #12212b, #3f769b);
}

