  -webkit-justify-content: center;
          justify-content: center;
  margin: 50px 0;
  background-color: inherit;
}

/* Style the tab */

/* Style the buttons inside the tab */
.tablinks {
  display: inline;
  background-color: inherit;
  color: rgb(77, 77, 77);
  padding: 15px 15px;
  border: none;
  outline: none;
  text-align: left;
  cursor: pointer;
  transition: 0.3s;
  font-size: 1.1rem;
  font-weight: 500;
}

/* Change background color of buttons on hover */
.tablinks:hover {
  background-color: rgb(220, 220, 220);
}

/* Create an active/current "tab button" class */
.tablinks.active {
  background-color: rgb(210, 210, 210);
  color: rgb(47, 82, 128);
}

.tabcontent {
  padding: 0px 12px;
}

.row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  width: 100%;
}

.col {
  margin: 0 2px;
  margin-bottom: 4px;
  width: 350px;
  height: 350px;
  object-fit: cover;
  -webkit-transform: scale(1);
          transform: scale(1);
  background-color: rgb(41, 41, 41);
  transition: all 0.5s ease-out;
}

.col:hover {
  -webkit-transform: scale(95%);
          transform: scale(95%);
}

.card-img {
  opacity: 0.85;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.5s ease;
}

.card-middle {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  width: 100%;
  transition: 0.5s ease;
  opacity: 0;
  text-align: center;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}

.card-link {
  position: relative;
  background-color: rgb(60, 110, 113);
  cursor: pointer;
  width: 50%;
  color: white;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 1.2rem;
  padding: 15px 10px;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  transition: all 0.3s ease-out;
}

.card-description {
  width: 90%;
  font-size: 1.2rem;
  color: rgb(200, 200, 200);
  margin-bottom: 50px;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  letter-spacing: 1px;
  line-height: 1.4;
}

.col:hover .card-img {
  opacity: 0.2;
}

.col:hover .card-middle {
  opacity: 1;
}

.col:hover .card-description {
  -webkit-transform: translate(0%, 0%);
          transform: translate(0%, 0%);
}

.col:hover .card-link {
  -webkit-transform: translate(0%, 0%);
          transform: translate(0%, 0%);
}

